  --color-blue: #2883ff;
  --color-green: #72d481;
  --color-red: #ff534e;
  --primary-color: var(--color-purple);
  --button-color: var(--color-orange);
  --button-hover-color: lighten(var(--color-orange), 10%);
  --white: #fff;
  --black: #000;
  --minor-second: 1.067;
  --major-second: 1.125;
  --minor-third: 1.2;
  --major-third: 1.25;
  --perfect-fourth: 1.333;
  --augmented-fourth: 1.414;
  --perfect-fifth: 1.5;
  --golden-ratio: 1.618;
  --type-scale: var(--augmented-fourth);
  --text-base: 16px;
  --font-family: sans-serif;
  --text-xxs: calc(var(--text-base) * var(--type-scale) / 2);
  --text-xs: calc(var(--text-base) * var(--type-scale) / 1.8);
  --text-sm: calc(var(--text-base) * var(--type-scale) / 1.6);
  --text-md: var(--text-base);
  --text-lg: calc(var(--text-base) * var(--type-scale));
  --text-xl: calc(var(--text-base) * var(--type-scale) * 1.65);
  --text-xxl: calc(var(--text-base) * var(--type-scale) * 2.4);
  --text-xxxl: calc(var(--text-base) * var(--type-scale) * 3);
  --text-regular: 300;
  --text-medium: 500;
  --text-semi-bold: 700;
  --text-bold: 900;
  --radius-sm: 2px;
  --radius-md: 4px;
  --radius-lg: 8px;
  --transition-fast: .1s;
  --transition-normal: .3s;
  --transition-slow: .6s;
  --animate-duration: 1s;
}

@media screen and (max-width: 768px) {
